    flex-wrap: wrap;
    gap: 30px;
  }
}
.promotion__other li {
  border: 7px solid;
  border-image: linear-gradient(to right bottom, #d10a42 0, #00359f 100%) 1;
  padding: 23px 28px 28px;
}
@media (max-width: 1365px) {
  .promotion__other li {
    padding: 23px 48px 28px 28px;
  }
}
@media (max-width: 767px) {
  .promotion__other li {
    text-align: center;
    align-items: center;
    padding: 39px 42px 33px;
  }
}
.promotion__other b {
  display: inline-block;
  margin-bottom: 10px;
}
.promotion__other p {
  font-size: 16px;
  line-height: 1.5;
  margin: 0;
  margin-bottom: 62px;
}
@media (max-width: 1365px) {
  .promotion__other p {
    min-height: 48px;
    margin-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .promotion__other p {
    font-size: 14px;
    line-height: 1.57;
    min-height: 44px;
    margin-bottom: 25px;
  }
}
.promotion__other a {
  color: #102654;
  border-bottom: 1px solid #102654;
}

