.supergames {
  padding: 120px 103px;

  @include vp-1365 {
    padding: 100px 45px;
  }

  @include vp-767 {
    padding: 70px 15px;
  }
}

.supergames__container {
  max-width: 1160px;
  margin: 0 auto;
  background-color: $color-torch-red;
  color: $color-default-white;
  display: grid;
  grid-template-columns: 500px 660px;

  @include vp-1365 {
    max-width: 678px;
    grid-template-columns: min-content 1fr;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    max-width: fit-content;
    flex-wrap: wrap;
  }
}

.supergames__beginning {
  span {
    font-family: $ff-robotocondensed-light;
    font-size: 18px;
    line-height: 1.3;
    border-bottom: 1px solid $color-default-white;
    padding-bottom: 8px;

    @include vp-1365 {
      padding: 0;
    }

    @include vp-767 {
      font-size: 16px;
      line-height: 1.25;
    }
  }

  strong {
    font-family: $ff-robotocondensed-bold;
    font-size: 40px;
    line-height: 1;
    padding-top: 4px;

    @include vp-1365 {
      font-size: 24px;
      padding-top: 2px;
      letter-spacing: 0;
    }
  }

  small {
    font-size: 14px;
    line-height: 1.35;
    padding-top: 8px;

    @include vp-1365 {
      font-size: 10px;
      line-height: 1;
      padding: 4px;
    }
  }
}

.supergames__info {
  padding: 80px;
  display: grid;
  grid-template-columns: 180px 92px;
  grid-template-rows: repeat(3, min-content);
  column-gap: 68px;
  row-gap:  23px;

  @include vp-1365 {
    padding: 40px 45px 45px;
    grid-template-columns: 106px 56px;
    column-gap: 40px;
    row-gap: 6px;

    @include vp-767 {
      grid-template-columns: 117px 56px;
      padding: 40px;
      column-gap: 37px;
      row-gap: 12px;
    }
  }

  h2 {
    font-family: $ff-alumnisans-semibold;
    font-size: 82px;
    line-height: 0.92;
    letter-spacing: 2.46px;
    text-transform: uppercase;
    margin: 0;

    @include vp-1365 {
      font-size: 48px;
      line-height: 0.96;
      letter-spacing: 1.44px;
    }
  }

  p {
    font-family: $ff-robotocondensed-light;
    font-size: 18px;
    line-height: 1.3;
    margin: 0;
    grid-column: 1/ -1;
    margin-bottom: 32px;

    @include vp-1365 {
      margin-bottom: 16px;
    }

    @include vp-767 {
      font-size: 16px;
      line-height: 1.25;
      padding-right: 30px;
      margin-bottom: 9px;
    }
  }

  a {
    grid-column: 1/ -1;
    justify-content: center;

    @include vp-1365 {
      padding: 20px 30px;
    }

    &:hover,
    &:focus {
      background-color: $color-default-white;
      color: $color-egyptian-blue;
    }

    &:active {
      background-color: $color-default-white;
      color: $color-sapphire;
    }
  }
}

.supergames__date {
  display: grid;
  grid-template-columns: 21px 37px;
  grid-template-rows: repeat(2, min-content);
  column-gap: 6px;
  margin-bottom: 18px;

  @include vp-1365 {
    grid-template-columns: 13px 26px;
    grid-template-rows: repeat(2, 24px);
    column-gap: 2px;
    margin-bottom: 10px;
  }

  @include vp-767 {
    grid-template-rows: repeat(2, 20px);
    column-gap: 0;
    margin-bottom: 13px;
  }

  span {
    grid-column: 1/ -1;
  };
}

.supergames__time {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
