.promotion {
  padding-top: 120px;
  padding-bottom: 120px;

  @include vp-1365 {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    padding-top: 71px;
    padding-bottom: 70px;
  }

  h2 {
    font-family: $ff-alumnisans-semibold;
    font-size: 54px;
    line-height: 1;
    letter-spacing: 1.62px;
    text-align: center;
    margin: 0;
    margin-bottom: 70px;

    @include vp-1365 {
      font-size: 42px;
      letter-spacing: 1.26px;
      margin-bottom: 40px;
    }

    @include vp-767 {
      font-size: 32px;
      letter-spacing: 0.96px;
      margin-bottom: 41px;
    }
  }
}

.promotion__wrapper {
  max-width: 1160px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 40px;

  @include vp-1365 {
    grid-template-columns: repeat(2, calc(324 / 678 * 100%));
    grid-template-rows: repeat(2, min-height);
    column-gap: calc(30 / 678 * 100%);
    row-gap: 30px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    row-gap: 40px;
  }

  b {
    font-family: $ff-robotocondensed-bold;
    font-size: 26px;
    line-height: 1;
    margin-bottom: 15px;

    @include vp-1365 {
      font-size: 24px;
    }

    @include vp-767 {
      font-size: 22px;
    }
  }

  strong {
    font-family: $ff-robotocondensed-bold;
    font-size: 70px;
    line-height: 1;
    margin-bottom: 26px;

    @include vp-1365 {
      margin-bottom: 21px;
    }

    @include vp-767 {
      font-size: 64px;
      margin-bottom: 20px;
    }
  }

  p {
    font-size: 18px;
    line-height: 1.33;
    margin: 0;
    margin-bottom: 175px;

    @include vp-1365 {
      margin-bottom: 187px;
    }

    @include vp-767 {
      font-size: 16px;
      line-height: 1.25;
      margin-bottom: 37px;
    }
  }

  a {
    font-family: $ff-robotocondensed-bold;
    font-size: 14px;
    line-height: 1;
    letter-spacing: 0.7px;
    text-transform: uppercase;
    color: $color-default-white;
    width: fit-content;
    padding-bottom: 2px;
    border-bottom: 1px solid $color-default-white;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.promotion__basic {
  display: grid;
  grid-template-columns: repeat(2, 50%);

  @include vp-1365 {
    grid-column: 1/ -1;
    grid-template-columns: auto 399px;
  }

  @include vp-767 {
    grid-template-columns: auto;
  }
}

.promotion__info {
  background-color: $color-torch-red;
  color: $color-default-white;
  padding: 40px 45px 45px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  @include vp-1365 {
    padding: 45px;
  }

  @include vp-767 {
    text-align: center;
    align-items: center;
    padding-top: 46px;
    padding-bottom: 46px;
  }

  b {
    margin-bottom: 15px;

    @include vp-1365 {
      margin-bottom: 5px;
    }

    @include vp-767 {
      margin-bottom: 8px;
    }
  }
}

.promotion__other {
  @include vp-1365 {
    grid-column: 1/ -1;
  }

  ul {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 40px;
    margin: 0;
    padding: 0;
    list-style: none;

    @include vp-1365 {
      display: grid;
      grid-template-columns: repeat(2, calc(324 / 678 * 100%));
      column-gap: calc(30 / 678 * 100%);
      width: 100%;
    }

    @include vp-767 {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      gap: 30px;
    }
  }

  li {
    border: 7px solid;
    border-image:  $border-gradient-1 1;
    padding: 23px 28px 28px;

    @include vp-1365 {
      padding: 23px 48px 28px 28px;
    }

    @include vp-767 {
      text-align: center;
      align-items: center;
      padding: 39px 42px 33px;
    }
  }

  b {
    display: inline-block;
    margin-bottom: 10px;
  }

  p {
    font-size: 16px;
    line-height: 1.5;
    margin: 0;
    margin-bottom: 62px;

    @include vp-1365 {
      min-height: 48px;
      margin-bottom: 40px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 1.57;
      min-height: 44px;
      margin-bottom: 25px;
    }
  }

  a {
    color: $color-sapphire;
    border-bottom: 1px solid $color-sapphire;
  }
}
